.dropdown {
     position: relative;
     display: inline-block;
}
 ul {
     list-style-type: none;
     margin: 0;
     padding: 0;
     top:45px;
     left:0px;
     width: 250px;
     background-color: white;
     font-weight:bold;
     position: absolute;
     -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
             box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
     z-index: 1;
     font-size: 12px;

}
 li a {
     color: #000;
     text-decoration: none;
}
 li {
     padding: 8px 16px;
     border-bottom: 1px solid #e5e5e5;
}
 li:last-child {
     border-bottom: none;
}
 li:hover {
     background-color: #e5e5e5;
     color: white;
}
 .button{
     width:178px;
     height:18px;
     background-color:#ff3232 ;
     padding:12px;
     /*margin:12px;*/
     border-radius:5px;
     font-weight:bold;
     color:white;
     text-align: center;
}
